

import { h, Component } from 'preact';
import  config from '../../../config'; 
const {ROOTDIR} = config;


export default class About extends Component {
    render(){

        const flexcontainer = {
            display: "flex",
            flexDirection: "row",
            flex: "1 1 auto",
            justifyContent: "center",
            minWidth: 504,
            overflow: "hidden"
        }

        const imagestyle = {
            flex: "0 0 auto",
            width: 460,
            boxShadow: "-6px 5px 10px 3px rgba(0,0,0,0.09)",
            padding: "20px",
            margin: "40px 0px 40px 0px",
        }

        const textstyle = {
            flex: "0 0 auto",
            width: 325,
            padding: "0px 0px 8px 0px",
            margin: "46px 0px 0px 60px",
            fontSize: "0.9em",
        }

        const textcontainer = {
            fontSize: "0.8em",
            background: "#3A7BD4",
            padding: 10,
            color: "white",
        }

        return <div style={flexcontainer}>
            <div style={imagestyle}>
                <img src={`${ROOTDIR}/databox/poster2.png`}/>
            </div>
            <div style={textstyle}>
            The <strong>DATABOX</strong> is a physical box that sits in a user's home and mediates access to a user's (principally IoT) personal data. The project is a collaboration with University of Nottingham, Cambridge University, Imperial College and various industrial partners, including the BBC.  As the number of devices that capture and record information about us continues to rise, a profound challenge has arisen around user privacy and data liability.  The databox is a local (linux) device that provided sandboxed access to a user's personal data using locally running privacy-preserving apps.  Instead of requiring that our data is shipped to the cloud for processing (the typical case), the databox model allows processing to move to the data, leaving data under the control of the data subject.  I have build a graphical (web-based) environment for constructing databox apps.
            </div>
        </div>

    }
}



