import style from '../style';
import { h, Component } from 'preact';
import Abstractions from '../abstractions';
import Test from '../test';
import Data from '../data';
import { Link } from 'preact-router/match';


const features = [
    {
        component: <Data/>,
        text: <div>IoT data will take many forms and can be challenging to work with.  All datastores describe their data using a json schema, and all nodes provide functions that generate new schemas based on their processing. This means that the IDE can automatically flag when incompatible nodes are connected together, or even ensure that downatream nodes will receive data in the correct format.  I also use Facebook's Flow in the browser to auto-generate type signatures for any "raw" javascript written in the IDE.</div>
    },
    {
        component: <Abstractions/>,
        text: <div> The IDE models an app as a set of connections between nodes, which are either: datastores (originators of data), processors (code that operates on data) and outputs (actions that occur, such as device actuation).  These simple abstractions can be used to create quite complex functionality.  Each node has a wealth of configuation options avaialble to it. The configurations interfaces can be <Link  href="/sc/project/uibuilder">complex</Link> tools in their own right. </div>
    },
    {
        component: <Test/>,
        text: <div>All apps built in my IDE can be tested against mock data.  When a user clicks 'test', client side code is pushed to the server, where it is wrapped in a docker container.  This docker container then communicates with a mock data generator, processes the  data then sends the result over a websocket to a browser window.  This makes it easy for users to design and evaluate their code prior to publication. </div>
    },
   

]
export default class My extends Component {
   
    render(){

            const container = {
                display: "flex",
                flexDirection: "row",
                flexWrap: "wrap",
                justifyContent: "center",
                overflowY: "auto",
                height: "calc(100vh - 300px)"
            }

            const featurecontainer = {
               display: "flex",
               flexDirection: "column",
               margin: 7,
               padding: 0,
               boxShadow: "0 1px 2px rgba(0,0,0,0.07)",
               padding: 10,
               width: 370,
            }
            const picture = {
                flex: "0 1 auto", 
                margin: "10px 10px 10px 0px"
            }

            const about = {
                padding: 10,
                flex: "0 1 auto",
                fontSize: "0.8em"
            }

            const separator = {
                marginBottom: 10,
            }

            const scroller ={
                height: "100%",
                overflowY: "auto",
            }
            const items = features.map((f,i)=>{
                return <div key={i} style={featurecontainer}>
                <div style={picture}>
                    {f.component} 
                </div>
                <div style={about}>
                    {f.text}
                </div>  
            </div>});

            return  (<div style={scroller}>
                        <div style={separator}>
                        I have built a web-based IDE for building apps that run on a dedicated <strong>databox</strong> in a user's home.  The development environment is flow-based; data flows across edges to nodes: black boxes of functionality.  The IDE automatically tracks the flow of personal data and flags it to the developer.  The IDE also provided an in-browser testing environment, context sensitive help and static code checking. Once published, the IDE builds a docker image of the app ready for deployment.
                        </div>
                        <div style={container}>
                            {items}
                        </div>

                    </div>)
    }
}


